$backgroundColor: #f4f4f4;
$color: #555;
$fontSize: 12px;
$fontWeight: 400;

.tooltip {
  width: 275px;
  height: 30px;
  background-color: $backgroundColor;
  color: $color;
  font-size: $fontSize;
  font-weight: $fontWeight;
  padding: 7px 6px;
  box-sizing: border-box;
  font-family: 'Noto Sans';
  letter-spacing: -1px;
  line-height: 16px;
}
